module.exports = {"markup":"<h1 id=\"philosophy\"><a href=\"/core/guides/philosophy\" class=\"internal-link\">Philosophy</a></h1>\n<p>This guide&#x2019;s purpose is to explain the mental model to have when using React Router. We call it &#x201C;Dynamic Routing&#x201D;, which is quite different from the &#x201C;Static Routing&#x201D; you&#x2019;re probably more familiar with.</p>\n<h2 id=\"philosophy-static-routing\"><a href=\"/core/guides/philosophy/static-routing\" class=\"internal-link\">Static Routing</a></h2>\n<p>If you&#x2019;ve used Rails, Express, Ember, Angular etc. you&#x2019;ve used static routing. In these frameworks, you declare your routes as part of your app&#x2019;s initialization before any rendering takes place. React Router pre-v4 was also static (mostly). Let&#x2019;s take a look at how to configure routes in express:</p>\n<pre><code class=\"language-js\">app<span class=\"token punctuation\">.</span><span class=\"token keyword\">get</span><span class=\"token punctuation\">(</span><span class=\"token string\">&apos;/&apos;</span><span class=\"token punctuation\">,</span> handleIndex<span class=\"token punctuation\">)</span>\napp<span class=\"token punctuation\">.</span><span class=\"token keyword\">get</span><span class=\"token punctuation\">(</span><span class=\"token string\">&apos;/invoices&apos;</span><span class=\"token punctuation\">,</span> handleInvoices<span class=\"token punctuation\">)</span>\napp<span class=\"token punctuation\">.</span><span class=\"token keyword\">get</span><span class=\"token punctuation\">(</span><span class=\"token string\">&apos;/invoices/:id&apos;</span><span class=\"token punctuation\">,</span> handleInvoice<span class=\"token punctuation\">)</span>\napp<span class=\"token punctuation\">.</span><span class=\"token keyword\">get</span><span class=\"token punctuation\">(</span><span class=\"token string\">&apos;/invoices/:id/edit&apos;</span><span class=\"token punctuation\">,</span> handleInvoiceEdit<span class=\"token punctuation\">)</span>\n\napp<span class=\"token punctuation\">.</span><span class=\"token function\">listen</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span>\n</code></pre>\n<p>Note how the routes are declared before the app listens. The client side routers we&#x2019;ve used are similar. In Angular you declare your routes up front and then import them to the top-level <code>AppModule</code> before rendering:</p>\n<pre><code class=\"language-js\"><span class=\"token keyword\">const</span> appRoutes<span class=\"token punctuation\">:</span> Routes <span class=\"token operator\">=</span> <span class=\"token punctuation\">[</span>\n  <span class=\"token punctuation\">{</span> path<span class=\"token punctuation\">:</span> <span class=\"token string\">&apos;crisis-center&apos;</span><span class=\"token punctuation\">,</span>\n    component<span class=\"token punctuation\">:</span> CrisisListComponent\n  <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span>\n  <span class=\"token punctuation\">{</span> path<span class=\"token punctuation\">:</span> <span class=\"token string\">&apos;hero/:id&apos;</span><span class=\"token punctuation\">,</span>\n    component<span class=\"token punctuation\">:</span> HeroDetailComponent\n  <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span>\n  <span class=\"token punctuation\">{</span> path<span class=\"token punctuation\">:</span> <span class=\"token string\">&apos;heroes&apos;</span><span class=\"token punctuation\">,</span>\n    component<span class=\"token punctuation\">:</span> HeroListComponent<span class=\"token punctuation\">,</span>\n    data<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span> title<span class=\"token punctuation\">:</span> <span class=\"token string\">&apos;Heroes List&apos;</span> <span class=\"token punctuation\">}</span>\n  <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span>\n  <span class=\"token punctuation\">{</span> path<span class=\"token punctuation\">:</span> <span class=\"token string\">&apos;&apos;</span><span class=\"token punctuation\">,</span>\n    redirectTo<span class=\"token punctuation\">:</span> <span class=\"token string\">&apos;/heroes&apos;</span><span class=\"token punctuation\">,</span>\n    pathMatch<span class=\"token punctuation\">:</span> <span class=\"token string\">&apos;full&apos;</span>\n  <span class=\"token punctuation\">}</span><span class=\"token punctuation\">,</span>\n  <span class=\"token punctuation\">{</span> path<span class=\"token punctuation\">:</span> <span class=\"token string\">&apos;**&apos;</span><span class=\"token punctuation\">,</span>\n    component<span class=\"token punctuation\">:</span> PageNotFoundComponent\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">]</span><span class=\"token punctuation\">;</span>\n\n@<span class=\"token function\">NgModule</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span>\n  imports<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">[</span>\n    RouterModule<span class=\"token punctuation\">.</span><span class=\"token function\">forRoot</span><span class=\"token punctuation\">(</span>appRoutes<span class=\"token punctuation\">)</span>\n  <span class=\"token punctuation\">]</span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span>\n\n<span class=\"token keyword\">export</span> <span class=\"token keyword\">class</span> <span class=\"token class-name\">AppModule</span> <span class=\"token punctuation\">{</span> <span class=\"token punctuation\">}</span>\n</code></pre>\n<p>Ember has a conventional <code>routes.js</code> file that the build reads and\nimports into the application for you. Again, this happens before\nyour app renders.</p>\n<pre><code class=\"language-js\">Router<span class=\"token punctuation\">.</span><span class=\"token function\">map</span><span class=\"token punctuation\">(</span><span class=\"token keyword\">function</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span><span class=\"token function\">route</span><span class=\"token punctuation\">(</span><span class=\"token string\">&apos;about&apos;</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n  <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span><span class=\"token function\">route</span><span class=\"token punctuation\">(</span><span class=\"token string\">&apos;contact&apos;</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n  <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span><span class=\"token function\">route</span><span class=\"token punctuation\">(</span><span class=\"token string\">&apos;rentals&apos;</span><span class=\"token punctuation\">,</span> <span class=\"token keyword\">function</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span><span class=\"token function\">route</span><span class=\"token punctuation\">(</span><span class=\"token string\">&apos;show&apos;</span><span class=\"token punctuation\">,</span> <span class=\"token punctuation\">{</span> path<span class=\"token punctuation\">:</span> <span class=\"token string\">&apos;/:rental_id&apos;</span> <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n  <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">;</span>\n\n<span class=\"token keyword\">export</span> <span class=\"token keyword\">default</span> Router\n</code></pre>\n<p>Though the APIs are different, they all share the model of &#x201C;static routes&#x201D;. React Router also followed that lead up until v4.</p>\n<p>To be successful with React Router, you need to forget all that! :O</p>\n<h2 id=\"philosophy-backstory\"><a href=\"/core/guides/philosophy/backstory\" class=\"internal-link\">Backstory</a></h2>\n<p>To be candid, we were pretty frustrated with the direction we&#x2019;d taken React Router by v2. We (Michael and Ryan) felt limited by the API, recognized we were reimplementing parts of React (lifecycles, and more), and it just didn&#x2019;t match the mental model React has given us for composing UI.</p>\n<p>We were walking through the hallway of a hotel just before a workshop discussing what to do about it. We asked each other: &#x201C;What would it look like if we built the router using the patterns we teach in our workshops?&#x201D;</p>\n<p>It was only a matter of hours into development that we had a proof-of-concept that we knew was the future we wanted for routing. We ended up with API that wasn&#x2019;t &#x201C;outside&#x201D; of React, an API that composed, or naturally fell into place, with the rest of React. We think you&#x2019;ll love it.</p>\n<h2 id=\"philosophy-dynamic-routing\"><a href=\"/core/guides/philosophy/dynamic-routing\" class=\"internal-link\">Dynamic Routing</a></h2>\n<p>When we say dynamic routing, we mean routing that takes place <strong>as your app is rendering</strong>, not in a configuration or convention outside of a running app.  That means almost everything is a component in React Router. Here&#x2019;s a 60 second review of the API to see how it works:</p>\n<p>First, grab yourself a <code>Router</code> component for the environment you&#x2019;re targeting and render it at the top of your app.</p>\n<pre><code class=\"language-jsx\"><span class=\"token comment\">// react-native</span>\n<span class=\"token keyword\">import</span> <span class=\"token punctuation\">{</span> NativeRouter <span class=\"token punctuation\">}</span> <span class=\"token keyword\">from</span> <span class=\"token string\">&apos;react-router-native&apos;</span>\n\n<span class=\"token comment\">// react-dom (what we&apos;ll use here)</span>\n<span class=\"token keyword\">import</span> <span class=\"token punctuation\">{</span> BrowserRouter <span class=\"token punctuation\">}</span> <span class=\"token keyword\">from</span> <span class=\"token string\">&apos;react-router-dom&apos;</span>\n\nReactDOM<span class=\"token punctuation\">.</span><span class=\"token function\">render</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">(</span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>BrowserRouter</span><span class=\"token punctuation\">&gt;</span></span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>App</span><span class=\"token punctuation\">/&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>BrowserRouter</span><span class=\"token punctuation\">&gt;</span></span>\n<span class=\"token punctuation\">)</span><span class=\"token punctuation\">,</span> el<span class=\"token punctuation\">)</span>\n</code></pre>\n<p>Next, grab the link component to link to a new location:</p>\n<pre><code class=\"language-jsx\"><span class=\"token keyword\">const</span> <span class=\"token function-variable function\">App</span> <span class=\"token operator\">=</span> <span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=&gt;</span> <span class=\"token punctuation\">(</span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span><span class=\"token punctuation\">&gt;</span></span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>nav</span><span class=\"token punctuation\">&gt;</span></span>\n      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Link</span> <span class=\"token attr-name\">to</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>/dashboard<span class=\"token punctuation\">&quot;</span></span><span class=\"token punctuation\">&gt;</span></span>Dashboard<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>Link</span><span class=\"token punctuation\">&gt;</span></span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>nav</span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">&gt;</span></span>\n<span class=\"token punctuation\">)</span>\n</code></pre>\n<p>Finally, render a <code>Route</code> to show some UI when the user visits\n<code>/dashboard</code>.</p>\n<pre><code class=\"language-jsx\"><span class=\"token keyword\">const</span> <span class=\"token function-variable function\">App</span> <span class=\"token operator\">=</span> <span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=&gt;</span> <span class=\"token punctuation\">(</span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span><span class=\"token punctuation\">&gt;</span></span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>nav</span><span class=\"token punctuation\">&gt;</span></span>\n      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Link</span> <span class=\"token attr-name\">to</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>/dashboard<span class=\"token punctuation\">&quot;</span></span><span class=\"token punctuation\">&gt;</span></span>Dashboard<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>Link</span><span class=\"token punctuation\">&gt;</span></span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>nav</span><span class=\"token punctuation\">&gt;</span></span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span><span class=\"token punctuation\">&gt;</span></span>\n      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">path</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>/dashboard<span class=\"token punctuation\">&quot;</span></span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>Dashboard<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">&gt;</span></span>\n<span class=\"token punctuation\">)</span>\n</code></pre>\n<p>The <code>Route</code> will render <code>&lt;Dashboard {...props}/&gt;</code> where <code>props</code> are some router specific things that look like <code>{ match, location, history }</code>. If the user is <strong>not</strong> at <code>/dashboard</code> then the <code>Route</code> will render <code>null</code>.  That&#x2019;s pretty much all there is to it.</p>\n<h2 id=\"philosophy-nested-routes\"><a href=\"/core/guides/philosophy/nested-routes\" class=\"internal-link\">Nested Routes</a></h2>\n<p>Lots of routers have some concept of &#x201C;nested routes&#x201D;. If you&#x2019;ve used versions of React Router previous to v4, you&#x2019;ll know it did too!  When you move from a static route configuration to dynamic, rendered routes, how do you &#x201C;nest routes&#x201D;? Well, how do you nest a <code>div</code>?</p>\n<pre><code class=\"language-jsx\"><span class=\"token keyword\">const</span> <span class=\"token function-variable function\">App</span> <span class=\"token operator\">=</span> <span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=&gt;</span> <span class=\"token punctuation\">(</span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>BrowserRouter</span><span class=\"token punctuation\">&gt;</span></span>\n    <span class=\"token punctuation\">{</span><span class=\"token comment\">/* here&apos;s a div */</span><span class=\"token punctuation\">}</span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span><span class=\"token punctuation\">&gt;</span></span>\n      <span class=\"token punctuation\">{</span><span class=\"token comment\">/* here&apos;s a Route */</span><span class=\"token punctuation\">}</span>\n      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">path</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>/tacos<span class=\"token punctuation\">&quot;</span></span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>Tacos<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>BrowserRouter</span><span class=\"token punctuation\">&gt;</span></span>\n<span class=\"token punctuation\">)</span>\n\n<span class=\"token comment\">// when the url matches `/tacos` this component renders</span>\n<span class=\"token keyword\">const</span> <span class=\"token function-variable function\">Tacos</span>  <span class=\"token operator\">=</span> <span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span> match <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=&gt;</span> <span class=\"token punctuation\">(</span>\n  <span class=\"token comment\">// here&apos;s a nested div</span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>div</span><span class=\"token punctuation\">&gt;</span></span>\n    <span class=\"token punctuation\">{</span><span class=\"token comment\">/* here&apos;s a nested Route,\n        match.url helps us make a relative path */</span><span class=\"token punctuation\">}</span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span>\n      <span class=\"token attr-name\">path</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>match<span class=\"token punctuation\">.</span>url <span class=\"token operator\">+</span> <span class=\"token string\">&apos;/carnitas&apos;</span><span class=\"token punctuation\">}</span></span>\n      <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>Carnitas<span class=\"token punctuation\">}</span></span>\n    <span class=\"token punctuation\">/&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>div</span><span class=\"token punctuation\">&gt;</span></span>\n<span class=\"token punctuation\">)</span>\n</code></pre>\n<p>See how the router has no &#x201C;nesting&#x201D; API? <code>Route</code> is just a component, just like <code>div</code>. So to nest a <code>Route</code> or a <code>div</code>, you just &#x2026; do it.</p>\n<p>Let&#x2019;s get trickier.</p>\n<h2 id=\"philosophy-responsive-routes\"><a href=\"/core/guides/philosophy/responsive-routes\" class=\"internal-link\">Responsive Routes</a></h2>\n<p>Consider a user navigates to <code>/invoices</code>.  Your app is adaptive to different screen sizes, they have a narrow viewport, and so you only show them the list of invoices and a link to the invoice dashboard. They can navigate deeper from there.</p>\n<pre><code class=\"language-asciidoc\">Small Screen\nurl: /invoices\n\n<span class=\"token inline\"><span class=\"token punctuation\">+</span>----------------------<span class=\"token punctuation\">+</span></span>\n|                      |\n|      Dashboard       |\n|                      |\n<span class=\"token inline\"><span class=\"token punctuation\">+</span>----------------------<span class=\"token punctuation\">+</span></span>\n|                      |\n|      Invoice 01      |\n|                      |\n<span class=\"token inline\"><span class=\"token punctuation\">+</span>----------------------<span class=\"token punctuation\">+</span></span>\n|                      |\n|      Invoice 02      |\n|                      |\n<span class=\"token inline\"><span class=\"token punctuation\">+</span>----------------------<span class=\"token punctuation\">+</span></span>\n|                      |\n|      Invoice 03      |\n|                      |\n<span class=\"token inline\"><span class=\"token punctuation\">+</span>----------------------<span class=\"token punctuation\">+</span></span>\n|                      |\n|      Invoice 04      |\n|                      |\n<span class=\"token inline\"><span class=\"token punctuation\">+</span>----------------------<span class=\"token punctuation\">+</span></span>\n</code></pre>\n<p>On a larger screen we&#x2019;d like to show a master-detail view where the navigation is on the left and the dashboard or specific invoices show up on the right.</p>\n<pre><code class=\"language-asciidoc\">Large Screen\nurl: /invoices/dashboard\n\n<span class=\"token inline\"><span class=\"token punctuation\">+</span>----------------------<span class=\"token punctuation\">+</span></span>---------------------------+\n|                      |                           |\n|      Dashboard       |                           |\n|                      |   Unpaid:             5   |\n<span class=\"token inline\"><span class=\"token punctuation\">+</span>----------------------<span class=\"token punctuation\">+</span></span>                           |\n|                      |   Balance:   $53,543.00   |\n|      Invoice 01      |                           |\n|                      |   Past Due:           2   |\n<span class=\"token inline\"><span class=\"token punctuation\">+</span>----------------------<span class=\"token punctuation\">+</span></span>                           |\n|                      |                           |\n|      Invoice 02      |                           |\n|                      |   <span class=\"token inline\"><span class=\"token punctuation\">+</span>-------------------<span class=\"token punctuation\">+</span></span>   |\n<span class=\"token inline\"><span class=\"token punctuation\">+</span>----------------------<span class=\"token punctuation\">+</span></span>   |                   |   |\n|                      |   |  +    +     +     |   |\n|      Invoice 03      |   |  | +  |     |     |   |\n|                      |   |  | |  |  +  |  +  |   |\n<span class=\"token inline\"><span class=\"token punctuation\">+</span>----------------------<span class=\"token punctuation\">+</span></span>   |  | |  |  |  |  |  |   |\n|                      |   <span class=\"token inline\"><span class=\"token punctuation\">+</span>--<span class=\"token punctuation\">+</span></span>-<span class=\"token inline\"><span class=\"token punctuation\">+</span>--<span class=\"token punctuation\">+</span></span>--<span class=\"token inline\"><span class=\"token punctuation\">+</span>--<span class=\"token punctuation\">+</span></span>--<span class=\"token inline\"><span class=\"token punctuation\">+</span>--<span class=\"token punctuation\">+</span></span>   |\n|      Invoice 04      |                           |\n|                      |                           |\n<span class=\"token inline\"><span class=\"token punctuation\">+</span>----------------------<span class=\"token punctuation\">+</span></span>---------------------------+\n</code></pre>\n<p>Now pause for a minute and think about the <code>/invoices</code> url for both screen sizes. Is it even a valid route for a large screen? What should we put on the right side?</p>\n<pre><code class=\"language-asciidoc\">Large Screen\nurl: /invoices\n<span class=\"token inline\"><span class=\"token punctuation\">+</span>----------------------<span class=\"token punctuation\">+</span></span>---------------------------+\n|                      |                           |\n|      Dashboard       |                           |\n|                      |                           |\n<span class=\"token inline\"><span class=\"token punctuation\">+</span>----------------------<span class=\"token punctuation\">+</span></span>                           |\n|                      |                           |\n|      Invoice 01      |                           |\n|                      |                           |\n<span class=\"token inline\"><span class=\"token punctuation\">+</span>----------------------<span class=\"token punctuation\">+</span></span>                           |\n|                      |                           |\n|      Invoice 02      |             ???           |\n|                      |                           |\n<span class=\"token inline\"><span class=\"token punctuation\">+</span>----------------------<span class=\"token punctuation\">+</span></span>                           |\n|                      |                           |\n|      Invoice 03      |                           |\n|                      |                           |\n<span class=\"token inline\"><span class=\"token punctuation\">+</span>----------------------<span class=\"token punctuation\">+</span></span>                           |\n|                      |                           |\n|      Invoice 04      |                           |\n|                      |                           |\n<span class=\"token inline\"><span class=\"token punctuation\">+</span>----------------------<span class=\"token punctuation\">+</span></span>---------------------------+\n</code></pre>\n<p>On a large screen, <code>/invoices</code> isn&#x2019;t a valid route, but on a small screen it is!  To make things more interesting, consider somebody with a giant phone. They could be looking at <code>/invoices</code> in portrait orientation and then rotate their phone to landscape. Suddenly, we have enough room to show the master-detail UI, so you ought to redirect right then!</p>\n<p>React Router&#x2019;s previous versions&#x2019; static routes didn&#x2019;t really have a composable answer for this. When routing is dynamic, however, you can declaratively compose this functionality. If you start thinking about routing as UI, not as static configuration, your intuition will lead you to the following code:</p>\n<pre><code class=\"language-js\"><span class=\"token keyword\">const</span> <span class=\"token function-variable function\">App</span> <span class=\"token operator\">=</span> <span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=&gt;</span> <span class=\"token punctuation\">(</span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>AppLayout</span><span class=\"token punctuation\">&gt;</span></span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">path</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>/invoices<span class=\"token punctuation\">&quot;</span></span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>Invoices<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>AppLayout</span><span class=\"token punctuation\">&gt;</span></span>\n<span class=\"token punctuation\">)</span>\n\n<span class=\"token keyword\">const</span> <span class=\"token function-variable function\">Invoices</span> <span class=\"token operator\">=</span> <span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=&gt;</span> <span class=\"token punctuation\">(</span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Layout</span><span class=\"token punctuation\">&gt;</span></span>\n\n    <span class=\"token punctuation\">{</span><span class=\"token comment\">/* always show the nav */</span><span class=\"token punctuation\">}</span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>InvoicesNav</span><span class=\"token punctuation\">/&gt;</span></span>\n\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Media</span> <span class=\"token attr-name\">query</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>PRETTY_SMALL<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">&gt;</span></span>\n      <span class=\"token punctuation\">{</span>screenIsSmall <span class=\"token operator\">=&gt;</span> screenIsSmall\n        <span class=\"token comment\">// small screen has no redirect</span>\n        <span class=\"token operator\">?</span> <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Switch</span><span class=\"token punctuation\">&gt;</span></span>\n            <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">exact</span> <span class=\"token attr-name\">path</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>/invoices/dashboard<span class=\"token punctuation\">&quot;</span></span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>Dashboard<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n            <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">path</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>/invoices/:id<span class=\"token punctuation\">&quot;</span></span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>Invoice<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n          <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>Switch</span><span class=\"token punctuation\">&gt;</span></span>\n        <span class=\"token comment\">// large screen does!</span>\n        <span class=\"token punctuation\">:</span> <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Switch</span><span class=\"token punctuation\">&gt;</span></span>\n            <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">exact</span> <span class=\"token attr-name\">path</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>/invoices/dashboard<span class=\"token punctuation\">&quot;</span></span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>Dashboard<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n            <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">path</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>/invoices/:id<span class=\"token punctuation\">&quot;</span></span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>Invoice<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n            <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Redirect</span> <span class=\"token attr-name\">from</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>/invoices<span class=\"token punctuation\">&quot;</span></span> <span class=\"token attr-name\">to</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>/invoices/dashboard<span class=\"token punctuation\">&quot;</span></span><span class=\"token punctuation\">/&gt;</span></span>\n          <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>Switch</span><span class=\"token punctuation\">&gt;</span></span>\n      <span class=\"token punctuation\">}</span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>Media</span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>Layout</span><span class=\"token punctuation\">&gt;</span></span>\n<span class=\"token punctuation\">)</span>\n</code></pre>\n<p>As the user rotates their phone from portrait to landscape, this code will automatically redirect them to the dashboard. <em>The set of valid routes change depending on the dynamic nature of a mobile device in a user&#x2019;s hands</em>.</p>\n<p>This is just one example. There are many others we could discuss but we&#x2019;ll sum it up with this advice: To get your intuition in line with React Router&#x2019;s, think about components, not static routes. Think about how to solve the problem with React&#x2019;s declarative composability because nearly every &#x201C;React Router question&#x201D; is probably a &#x201C;React question&#x201D;.</p>\n","headers":[{"text":"Static Routing","slug":"static-routing"},{"text":"Backstory","slug":"backstory"},{"text":"Dynamic Routing","slug":"dynamic-routing"},{"text":"Nested Routes","slug":"nested-routes"},{"text":"Responsive Routes","slug":"responsive-routes"}],"title":{"text":"Philosophy","slug":"philosophy"}}

