module.exports = {"markup":"<h1 id=\"MemoryRouter\"><a href=\"/core/api/MemoryRouter\" class=\"internal-link\">&lt;MemoryRouter&gt;</a></h1>\n<p>A <a href=\"Router.md\"><code>&lt;Router&gt;</code></a> that keeps the history of your &#x201C;URL&#x201D; in memory (does not read or write to the address bar). Useful in tests and non-browser environments like <a href=\"https://facebook.github.io/react-native/\">React Native</a>.</p>\n<pre><code class=\"language-jsx\"><span class=\"token keyword\">import</span> <span class=\"token punctuation\">{</span> MemoryRouter <span class=\"token punctuation\">}</span> <span class=\"token keyword\">from</span> <span class=\"token string\">&apos;react-router&apos;</span>\n\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>MemoryRouter</span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>App</span><span class=\"token punctuation\">/&gt;</span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>MemoryRouter</span><span class=\"token punctuation\">&gt;</span></span>\n</code></pre>\n<h2 id=\"MemoryRouter-initialentries-array\"><a href=\"/core/api/MemoryRouter/initialentries-array\" class=\"internal-link\">initialEntries: array</a></h2>\n<p>An array of <code>location</code>s in the history stack. These may be full-blown location objects with <code>{ pathname, search, hash, state }</code> or simple string URLs.</p>\n<pre><code class=\"language-jsx\"><span class=\"token operator\">&lt;</span>MemoryRouter\n  initialEntries<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span><span class=\"token punctuation\">[</span> <span class=\"token string\">&apos;/one&apos;</span><span class=\"token punctuation\">,</span> <span class=\"token string\">&apos;/two&apos;</span><span class=\"token punctuation\">,</span> <span class=\"token punctuation\">{</span> pathname<span class=\"token punctuation\">:</span> <span class=\"token string\">&apos;/three&apos;</span> <span class=\"token punctuation\">}</span> <span class=\"token punctuation\">]</span><span class=\"token punctuation\">}</span>\n  initialIndex<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span><span class=\"token number\">1</span><span class=\"token punctuation\">}</span>\n<span class=\"token operator\">&gt;</span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>App</span><span class=\"token punctuation\">/&gt;</span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>MemoryRouter</span><span class=\"token punctuation\">&gt;</span></span>\n</code></pre>\n<h2 id=\"MemoryRouter-initialindex-number\"><a href=\"/core/api/MemoryRouter/initialindex-number\" class=\"internal-link\">initialIndex: number</a></h2>\n<p>The initial location&#x2019;s index in the array of <code>initialEntries</code>.</p>\n<h2 id=\"MemoryRouter-getuserconfirmation-func\"><a href=\"/core/api/MemoryRouter/getuserconfirmation-func\" class=\"internal-link\">getUserConfirmation: func</a></h2>\n<p>A function to use to confirm navigation. You must use this option when using <code>&lt;MemoryRouter&gt;</code> directly with a <code>&lt;Prompt&gt;</code>.</p>\n<h2 id=\"MemoryRouter-keylength-number\"><a href=\"/core/api/MemoryRouter/keylength-number\" class=\"internal-link\">keyLength: number</a></h2>\n<p>The length of <code>location.key</code>. Defaults to 6.</p>\n<pre><code class=\"language-jsx\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>MemoryRouter</span> <span class=\"token attr-name\">keyLength</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span><span class=\"token number\">12</span><span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n</code></pre>\n<h2 id=\"MemoryRouter-children-node\"><a href=\"/core/api/MemoryRouter/children-node\" class=\"internal-link\">children: node</a></h2>\n<p>A <a href=\"https://facebook.github.io/react/docs/react-api.html#react.children.only\">single child element</a> to render.</p>\n","headers":[{"text":"initialEntries: array","slug":"initialentries-array"},{"text":"initialIndex: number","slug":"initialindex-number"},{"text":"getUserConfirmation: func","slug":"getuserconfirmation-func"},{"text":"keyLength: number","slug":"keylength-number"},{"text":"children: node","slug":"children-node"}],"title":{"text":"<MemoryRouter>","slug":"MemoryRouter"}}

