module.exports = {"markup":"<h1 id=\"Prompt\"><a href=\"/core/api/Prompt\" class=\"internal-link\">&lt;Prompt&gt;</a></h1>\n<p>Used to prompt the user before navigating away from a page. When your application enters a state that should prevent the user from navigating away (like a form is half-filled out), render a <code>&lt;Prompt&gt;</code>.</p>\n<pre><code class=\"language-jsx\"><span class=\"token keyword\">import</span> <span class=\"token punctuation\">{</span> Prompt <span class=\"token punctuation\">}</span> <span class=\"token keyword\">from</span> <span class=\"token string\">&apos;react-router&apos;</span>\n\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Prompt</span>\n  <span class=\"token attr-name\">when</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>formIsHalfFilledOut<span class=\"token punctuation\">}</span></span>\n  <span class=\"token attr-name\">message</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>Are you sure you want to leave?<span class=\"token punctuation\">&quot;</span></span>\n<span class=\"token punctuation\">/&gt;</span></span>\n</code></pre>\n<h2 id=\"Prompt-message-string\"><a href=\"/core/api/Prompt/message-string\" class=\"internal-link\">message: string</a></h2>\n<p>The message to prompt the user with when they try to navigate away.</p>\n<pre><code class=\"language-jsx\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Prompt</span> <span class=\"token attr-name\">message</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>Are you sure you want to leave?<span class=\"token punctuation\">&quot;</span></span><span class=\"token punctuation\">/&gt;</span></span>\n</code></pre>\n<h2 id=\"Prompt-message-func\"><a href=\"/core/api/Prompt/message-func\" class=\"internal-link\">message: func</a></h2>\n<p>Will be called with the next <code>location</code> and <code>action</code> the user is attempting to navigate to. Return a string to show a prompt to the user or <code>true</code> to allow the transition.</p>\n<pre><code class=\"language-jsx\"><span class=\"token operator\">&lt;</span>Prompt message<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span>location <span class=\"token operator\">=&gt;</span> <span class=\"token punctuation\">(</span>\n  location<span class=\"token punctuation\">.</span>pathname<span class=\"token punctuation\">.</span><span class=\"token function\">startsWith</span><span class=\"token punctuation\">(</span><span class=\"token string\">&apos;/app&apos;</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">?</span> <span class=\"token boolean\">true</span> <span class=\"token punctuation\">:</span> <span class=\"token template-string\"><span class=\"token string\">`Are you sure you want to go to </span><span class=\"token interpolation\"><span class=\"token interpolation-punctuation punctuation\">${</span>location<span class=\"token punctuation\">.</span>pathname<span class=\"token interpolation-punctuation punctuation\">}</span></span><span class=\"token string\">?`</span></span>\n<span class=\"token punctuation\">)</span><span class=\"token punctuation\">}</span><span class=\"token operator\">/</span><span class=\"token operator\">&gt;</span>\n</code></pre>\n<h2 id=\"Prompt-when-bool\"><a href=\"/core/api/Prompt/when-bool\" class=\"internal-link\">when: bool</a></h2>\n<p>Instead of conditionally rendering a <code>&lt;Prompt&gt;</code> behind a guard, you can always render it but pass <code>when={true}</code> or <code>when={false}</code> to prevent or allow navigation accordingly.</p>\n<pre><code class=\"language-jsx\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Prompt</span> <span class=\"token attr-name\">when</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>formIsHalfFilledOut<span class=\"token punctuation\">}</span></span> <span class=\"token attr-name\">message</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>Are you sure?<span class=\"token punctuation\">&quot;</span></span><span class=\"token punctuation\">/&gt;</span></span>\n</code></pre>\n","headers":[{"text":"message: string","slug":"message-string"},{"text":"message: func","slug":"message-func"},{"text":"when: bool","slug":"when-bool"}],"title":{"text":"<Prompt>","slug":"Prompt"}}

