module.exports = {"markup":"<h1 id=\"Redirect\"><a href=\"/core/api/Redirect\" class=\"internal-link\">&lt;Redirect&gt;</a></h1>\n<p>Rendering a <code>&lt;Redirect&gt;</code> will navigate to a new location. The new location will override the current location in the history stack, like server-side redirects (HTTP 3xx) do.</p>\n<pre><code class=\"language-jsx\"><span class=\"token keyword\">import</span> <span class=\"token punctuation\">{</span> Route<span class=\"token punctuation\">,</span> Redirect <span class=\"token punctuation\">}</span> <span class=\"token keyword\">from</span> <span class=\"token string\">&apos;react-router&apos;</span>\n\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">exact</span> <span class=\"token attr-name\">path</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>/<span class=\"token punctuation\">&quot;</span></span> <span class=\"token attr-name\">render</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=&gt;</span> <span class=\"token punctuation\">(</span>\n  loggedIn <span class=\"token operator\">?</span> <span class=\"token punctuation\">(</span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Redirect</span> <span class=\"token attr-name\">to</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>/dashboard<span class=\"token punctuation\">&quot;</span></span><span class=\"token punctuation\">/&gt;</span></span>\n  <span class=\"token punctuation\">)</span> <span class=\"token punctuation\">:</span> <span class=\"token punctuation\">(</span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>PublicHomePage</span><span class=\"token punctuation\">/&gt;</span></span>\n  <span class=\"token punctuation\">)</span>\n<span class=\"token punctuation\">)</span><span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n</code></pre>\n<h2 id=\"Redirect-to-string\"><a href=\"/core/api/Redirect/to-string\" class=\"internal-link\">to: string</a></h2>\n<p>The URL to redirect to. Any valid URL path that <a href=\"https://www.npmjs.com/package/path-to-regexp\"><code>path-to-regexp</code></a> understands.\nAll URL parameters that are used in <code>to</code> must be covered by <code>from</code>.</p>\n<pre><code class=\"language-jsx\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Redirect</span> <span class=\"token attr-name\">to</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>/somewhere/else<span class=\"token punctuation\">&quot;</span></span><span class=\"token punctuation\">/&gt;</span></span>\n</code></pre>\n<h2 id=\"Redirect-to-object\"><a href=\"/core/api/Redirect/to-object\" class=\"internal-link\">to: object</a></h2>\n<p>A location to redirect to. <code>pathname</code> can be any valid URL path that <a href=\"https://www.npmjs.com/package/path-to-regexp\"><code>path-to-regexp</code></a> understands.</p>\n<pre><code class=\"language-jsx\"><span class=\"token operator\">&lt;</span>Redirect to<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span><span class=\"token punctuation\">{</span>\n  pathname<span class=\"token punctuation\">:</span> <span class=\"token string\">&apos;/login&apos;</span><span class=\"token punctuation\">,</span>\n  search<span class=\"token punctuation\">:</span> <span class=\"token string\">&apos;?utm=your+face&apos;</span><span class=\"token punctuation\">,</span>\n  state<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span> referrer<span class=\"token punctuation\">:</span> currentLocation <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">}</span><span class=\"token operator\">/</span><span class=\"token operator\">&gt;</span>\n</code></pre>\n<h2 id=\"Redirect-push-bool\"><a href=\"/core/api/Redirect/push-bool\" class=\"internal-link\">push: bool</a></h2>\n<p>When <code>true</code>, redirecting will push a new entry onto the history instead of replacing the current one.</p>\n<pre><code class=\"language-jsx\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Redirect</span> <span class=\"token attr-name\">push</span> <span class=\"token attr-name\">to</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>/somewhere/else<span class=\"token punctuation\">&quot;</span></span><span class=\"token punctuation\">/&gt;</span></span>\n</code></pre>\n<h2 id=\"Redirect-from-string\"><a href=\"/core/api/Redirect/from-string\" class=\"internal-link\">from: string</a></h2>\n<p>A pathname to redirect from. Any valid URL path that <a href=\"https://www.npmjs.com/package/path-to-regexp\"><code>path-to-regexp</code></a> understands.\nAll matched URL parameters are provided to the pattern in <code>to</code>. Must contain all parameters that are used in <code>to</code>. Additional parameters not used by <code>to</code> are ignored.</p>\n<p>This can only be used to match a location when rendering a <code>&lt;Redirect&gt;</code> inside of a <code>&lt;Switch&gt;</code>. See <a href=\"/core/api/Switch/children-node\" class=\"internal-link\"><code>&lt;Switch children&gt;</code></a> for more details.</p>\n<pre><code class=\"language-jsx\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Switch</span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Redirect</span> <span class=\"token attr-name\">from</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&apos;</span>/old-path<span class=\"token punctuation\">&apos;</span></span> <span class=\"token attr-name\">to</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&apos;</span>/new-path<span class=\"token punctuation\">&apos;</span></span><span class=\"token punctuation\">/&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">path</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&apos;</span>/new-path<span class=\"token punctuation\">&apos;</span></span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>Place<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>Switch</span><span class=\"token punctuation\">&gt;</span></span>\n</code></pre>\n<pre><code class=\"language-jsx\"><span class=\"token comment\">// Redirect with matched parameters</span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Switch</span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Redirect</span> <span class=\"token attr-name\">from</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&apos;</span>/users/:id<span class=\"token punctuation\">&apos;</span></span> <span class=\"token attr-name\">to</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&apos;</span>/users/profile/:id<span class=\"token punctuation\">&apos;</span></span><span class=\"token punctuation\">/&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Route</span> <span class=\"token attr-name\">path</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&apos;</span>/users/profile/:id<span class=\"token punctuation\">&apos;</span></span> <span class=\"token attr-name\">component</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>Profile<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>Switch</span><span class=\"token punctuation\">&gt;</span></span>\n</code></pre>\n<h2 id=\"Redirect-exact-bool\"><a href=\"/core/api/Redirect/exact-bool\" class=\"internal-link\">exact: bool</a></h2>\n<p>Match <code>from</code> exactly; equivalent to <a href=\"/core/api/Route/exact-bool\" class=\"internal-link\">Route.exact</a>.</p>\n<h2 id=\"Redirect-strict-bool\"><a href=\"/core/api/Redirect/strict-bool\" class=\"internal-link\">strict: bool</a></h2>\n<p>Match <code>from</code> strictly; equivalent to <a href=\"/core/api/Route/strict-bool\" class=\"internal-link\">Route.strict</a>.</p>\n","headers":[{"text":"to: string","slug":"to-string"},{"text":"to: object","slug":"to-object"},{"text":"push: bool","slug":"push-bool"},{"text":"from: string","slug":"from-string"},{"text":"exact: bool","slug":"exact-bool"},{"text":"strict: bool","slug":"strict-bool"}],"title":{"text":"<Redirect>","slug":"Redirect"}}

