module.exports = {"markup":"<h1 id=\"StaticRouter\"><a href=\"/core/api/StaticRouter\" class=\"internal-link\">&lt;StaticRouter&gt;</a></h1>\n<p>A <a href=\"Router.md\"><code>&lt;Router&gt;</code></a> that never changes location.</p>\n<p>This can be useful in server-side rendering scenarios when the user isn&#x2019;t actually clicking around, so the location never actually changes. Hence, the name: static. It&#x2019;s also useful in simple tests when you just need to plug in a location and make assertions on the render output.</p>\n<p>Here&#x2019;s an example node server that sends a 302 status code for <a href=\"Redirect.md\"><code>&lt;Redirect&gt;</code></a>s and regular HTML for other requests:</p>\n<pre><code class=\"language-jsx\"><span class=\"token keyword\">import</span> <span class=\"token punctuation\">{</span> createServer <span class=\"token punctuation\">}</span> <span class=\"token keyword\">from</span> <span class=\"token string\">&apos;http&apos;</span>\n<span class=\"token keyword\">import</span> React <span class=\"token keyword\">from</span> <span class=\"token string\">&apos;react&apos;</span>\n<span class=\"token keyword\">import</span> ReactDOMServer <span class=\"token keyword\">from</span> <span class=\"token string\">&apos;react-dom/server&apos;</span>\n<span class=\"token keyword\">import</span> <span class=\"token punctuation\">{</span> StaticRouter <span class=\"token punctuation\">}</span> <span class=\"token keyword\">from</span> <span class=\"token string\">&apos;react-router&apos;</span>\n\n<span class=\"token function\">createServer</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">(</span>req<span class=\"token punctuation\">,</span> res<span class=\"token punctuation\">)</span> <span class=\"token operator\">=&gt;</span> <span class=\"token punctuation\">{</span>\n\n  <span class=\"token comment\">// This context object contains the results of the render</span>\n  <span class=\"token keyword\">const</span> context <span class=\"token operator\">=</span> <span class=\"token punctuation\">{</span><span class=\"token punctuation\">}</span>\n\n  <span class=\"token keyword\">const</span> html <span class=\"token operator\">=</span> ReactDOMServer<span class=\"token punctuation\">.</span><span class=\"token function\">renderToString</span><span class=\"token punctuation\">(</span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>StaticRouter</span> <span class=\"token attr-name\">location</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>req<span class=\"token punctuation\">.</span>url<span class=\"token punctuation\">}</span></span> <span class=\"token attr-name\">context</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>context<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">&gt;</span></span>\n      <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>App</span><span class=\"token punctuation\">/&gt;</span></span>\n    <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>StaticRouter</span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token punctuation\">)</span>\n\n  <span class=\"token comment\">// context.url will contain the URL to redirect to if a &lt;Redirect&gt; was used</span>\n  <span class=\"token keyword\">if</span> <span class=\"token punctuation\">(</span>context<span class=\"token punctuation\">.</span>url<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    res<span class=\"token punctuation\">.</span><span class=\"token function\">writeHead</span><span class=\"token punctuation\">(</span><span class=\"token number\">302</span><span class=\"token punctuation\">,</span> <span class=\"token punctuation\">{</span>\n      Location<span class=\"token punctuation\">:</span> context<span class=\"token punctuation\">.</span>url\n    <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span>\n    res<span class=\"token punctuation\">.</span><span class=\"token function\">end</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span>\n  <span class=\"token punctuation\">}</span> <span class=\"token keyword\">else</span> <span class=\"token punctuation\">{</span>\n    res<span class=\"token punctuation\">.</span><span class=\"token function\">write</span><span class=\"token punctuation\">(</span>html<span class=\"token punctuation\">)</span>\n    res<span class=\"token punctuation\">.</span><span class=\"token function\">end</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">)</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span><span class=\"token punctuation\">.</span><span class=\"token function\">listen</span><span class=\"token punctuation\">(</span><span class=\"token number\">3000</span><span class=\"token punctuation\">)</span>\n</code></pre>\n<h2 id=\"StaticRouter-basename-string\"><a href=\"/core/api/StaticRouter/basename-string\" class=\"internal-link\">basename: string</a></h2>\n<p>The base URL for all locations. A properly formatted basename should have a leading slash, but no trailing slash.</p>\n<pre><code class=\"language-jsx\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>StaticRouter</span> <span class=\"token attr-name\">basename</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>/calendar<span class=\"token punctuation\">&quot;</span></span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Link</span> <span class=\"token attr-name\">to</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>/today<span class=\"token punctuation\">&quot;</span></span><span class=\"token punctuation\">/&gt;</span></span> <span class=\"token comment\">// renders &lt;a href=&quot;/calendar/today&quot;&gt;</span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>StaticRouter</span><span class=\"token punctuation\">&gt;</span></span>\n</code></pre>\n<h2 id=\"StaticRouter-location-string\"><a href=\"/core/api/StaticRouter/location-string\" class=\"internal-link\">location: string</a></h2>\n<p>The URL the server received, probably <code>req.url</code> on a node server.</p>\n<pre><code class=\"language-jsx\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>StaticRouter</span> <span class=\"token attr-name\">location</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>req<span class=\"token punctuation\">.</span>url<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>App</span><span class=\"token punctuation\">/&gt;</span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>StaticRouter</span><span class=\"token punctuation\">&gt;</span></span>\n</code></pre>\n<h2 id=\"StaticRouter-location-object\"><a href=\"/core/api/StaticRouter/location-object\" class=\"internal-link\">location: object</a></h2>\n<p>A location object shaped like <code>{ pathname, search, hash, state }</code></p>\n<pre><code class=\"language-jsx\"><span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>StaticRouter</span> <span class=\"token attr-name\">location</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span><span class=\"token punctuation\">{</span> pathname<span class=\"token punctuation\">:</span> <span class=\"token string\">&apos;/bubblegum&apos;</span> <span class=\"token punctuation\">}</span><span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>App</span><span class=\"token punctuation\">/&gt;</span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>StaticRouter</span><span class=\"token punctuation\">&gt;</span></span>\n</code></pre>\n<h2 id=\"StaticRouter-context-object\"><a href=\"/core/api/StaticRouter/context-object\" class=\"internal-link\">context: object</a></h2>\n<p>A plain JavaScript object. During the render, components can add properties to the object to store information about the render.</p>\n<pre><code class=\"language-jsx\"><span class=\"token keyword\">const</span> context <span class=\"token operator\">=</span> <span class=\"token punctuation\">{</span><span class=\"token punctuation\">}</span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>StaticRouter</span> <span class=\"token attr-name\">context</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>context<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">&gt;</span></span>\n  <span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>App</span> <span class=\"token punctuation\">/&gt;</span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;/</span>StaticRouter</span><span class=\"token punctuation\">&gt;</span></span>\n</code></pre>\n<p>When a <code>&lt;Route&gt;</code> matches, it will pass the context object to the component it renders as the <code>staticContext</code> prop. Check out the <a href=\"/web/guides/server-rendering\" class=\"internal-link\">Server Rendering guide</a> for more information on how to do this yourself.</p>\n<p>After the render, these properties can be used to to configure the server&#x2019;s response.</p>\n<pre><code class=\"language-js\"><span class=\"token keyword\">if</span><span class=\"token punctuation\">(</span>context<span class=\"token punctuation\">.</span>status <span class=\"token operator\">===</span> <span class=\"token string\">&apos;404&apos;</span><span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token comment\">// ...</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n<h2 id=\"StaticRouter-children-node\"><a href=\"/core/api/StaticRouter/children-node\" class=\"internal-link\">children: node</a></h2>\n<p>A <a href=\"https://facebook.github.io/react/docs/react-api.html#react.children.only\">single child element</a> to render.</p>\n","headers":[{"text":"basename: string","slug":"basename-string"},{"text":"location: string","slug":"location-string"},{"text":"location: object","slug":"location-object"},{"text":"context: object","slug":"context-object"},{"text":"children: node","slug":"children-node"}],"title":{"text":"<StaticRouter>","slug":"StaticRouter"}}

