module.exports = {"markup":"<h1 id=\"location\"><a href=\"/core/api/location\" class=\"internal-link\">location</a></h1>\n<p>Locations represent where the app is now, where you want it to go, or\neven where it was. It looks like this:</p>\n<pre><code class=\"language-js\"><span class=\"token punctuation\">{</span>\n  key<span class=\"token punctuation\">:</span> <span class=\"token string\">&apos;ac3df4&apos;</span><span class=\"token punctuation\">,</span> <span class=\"token comment\">// not with HashHistory!</span>\n  pathname<span class=\"token punctuation\">:</span> <span class=\"token string\">&apos;/somewhere&apos;</span>\n  search<span class=\"token punctuation\">:</span> <span class=\"token string\">&apos;?some=search-string&apos;</span><span class=\"token punctuation\">,</span>\n  hash<span class=\"token punctuation\">:</span> <span class=\"token string\">&apos;#howdy&apos;</span><span class=\"token punctuation\">,</span>\n  state<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token punctuation\">[</span>userDefined<span class=\"token punctuation\">]</span><span class=\"token punctuation\">:</span> <span class=\"token boolean\">true</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n<p>The router will provide you with a location object in a few places:</p>\n<ul>\n<li><a href=\"/core/api/Route/component\" class=\"internal-link\">Route component</a> as <code>this.props.location</code></li>\n<li><a href=\"/core/api/Route/render-func\" class=\"internal-link\">Route render</a> as <code>({ location }) =&gt; ()</code></li>\n<li><a href=\"/core/api/Route/children-func\" class=\"internal-link\">Route children</a> as <code>({ location }) =&gt; ()</code></li>\n<li><a href=\"/core/api/withRouter\" class=\"internal-link\">withRouter</a> as <code>this.props.location</code></li>\n</ul>\n<p>It is also found on <code>history.location</code> but you shouldn&#x2019;t use that because its mutable. You can read more about that in the <a href=\"/core/api/history\" class=\"internal-link\">history</a> doc.</p>\n<p>A location object is never mutated so you can use it in the lifecycle hooks to determine when navigation happens, this is really useful for data fetching and animation.</p>\n<pre><code class=\"language-js\"><span class=\"token function\">componentWillReceiveProps</span><span class=\"token punctuation\">(</span>nextProps<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n  <span class=\"token keyword\">if</span> <span class=\"token punctuation\">(</span>nextProps<span class=\"token punctuation\">.</span>location <span class=\"token operator\">!==</span> <span class=\"token keyword\">this</span><span class=\"token punctuation\">.</span>props<span class=\"token punctuation\">.</span>location<span class=\"token punctuation\">)</span> <span class=\"token punctuation\">{</span>\n    <span class=\"token comment\">// navigated!</span>\n  <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span>\n</code></pre>\n<p>You can provide locations instead of strings to the various places that navigate:</p>\n<ul>\n<li>Web <a href=\"/web/api/Link/to\" class=\"internal-link\">Link to</a></li>\n<li>Native <a href=\"/native/api/Link/to\" class=\"internal-link\">Link to</a></li>\n<li><a href=\"/core/api/Redirect/to\" class=\"internal-link\">Redirect to</a></li>\n<li><a href=\"/core/api/history/push\" class=\"internal-link\">history.push</a></li>\n<li><a href=\"/core/api/history/push\" class=\"internal-link\">history.replace</a></li>\n</ul>\n<p>Normally you just use a string, but if you need to add some &#x201C;location state&#x201D; that will be available whenever the app returns to that specific location, you can use a location object instead. This is useful if you want to branch UI based on navigation history instead of just paths (like modals).</p>\n<pre><code class=\"language-jsx\"><span class=\"token comment\">// usually all you need</span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Link</span> <span class=\"token attr-name\">to</span><span class=\"token attr-value\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">&quot;</span>/somewhere<span class=\"token punctuation\">&quot;</span></span><span class=\"token punctuation\">/&gt;</span></span>\n\n<span class=\"token comment\">// but you can use a location instead</span>\n<span class=\"token keyword\">const</span> location <span class=\"token operator\">=</span> <span class=\"token punctuation\">{</span>\n  pathname<span class=\"token punctuation\">:</span> <span class=\"token string\">&apos;/somewhere&apos;</span><span class=\"token punctuation\">,</span>\n  state<span class=\"token punctuation\">:</span> <span class=\"token punctuation\">{</span> fromDashboard<span class=\"token punctuation\">:</span> <span class=\"token boolean\">true</span> <span class=\"token punctuation\">}</span>\n<span class=\"token punctuation\">}</span>\n\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Link</span> <span class=\"token attr-name\">to</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>location<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\n<span class=\"token tag\"><span class=\"token tag\"><span class=\"token punctuation\">&lt;</span>Redirect</span> <span class=\"token attr-name\">to</span><span class=\"token script language-javascript\"><span class=\"token punctuation\">=</span><span class=\"token punctuation\">{</span>location<span class=\"token punctuation\">}</span></span><span class=\"token punctuation\">/&gt;</span></span>\nhistory<span class=\"token punctuation\">.</span><span class=\"token function\">push</span><span class=\"token punctuation\">(</span>location<span class=\"token punctuation\">)</span>\nhistory<span class=\"token punctuation\">.</span><span class=\"token function\">replace</span><span class=\"token punctuation\">(</span>location<span class=\"token punctuation\">)</span>\n</code></pre>\n<p>Finally, you can pass a location to the following components:</p>\n<ul>\n<li><a href=\"/core/api/Route/location\" class=\"internal-link\">Route</a></li>\n<li><a href=\"/core/api/Switch/location\" class=\"internal-link\">Switch</a></li>\n</ul>\n<p>This will prevent them from using the actual location in the router&#x2019;s state. This is useful for animation and pending navigation, or any time you want to trick a component into rendering at a different location than the real one.</p>\n","headers":[],"title":{"text":"location","slug":"location"}}

