module.exports = {"markup":"<h1 id=\"match\"><a href=\"/core/api/match\" class=\"internal-link\">match</a></h1>\n<p>A <code>match</code> object contains information about how a <code>&lt;Route path&gt;</code> matched the URL. <code>match</code> objects contain the following properties:</p>\n<ul>\n<li><code>params</code> - (object) Key/value pairs parsed from the URL corresponding to the dynamic segments of the path</li>\n<li><code>isExact</code> - (boolean) <code>true</code> if the entire URL was matched (no trailing characters)</li>\n<li><code>path</code> - (string) The path pattern used to match. Useful for building nested <code>&lt;Route&gt;</code>s</li>\n<li><code>url</code> - (string) The matched portion of the URL. Useful for building nested <code>&lt;Link&gt;</code>s</li>\n</ul>\n<p>You&#x2019;ll have access <code>match</code> objects in various places:</p>\n<ul>\n<li><a href=\"/core/api/Route/component\" class=\"internal-link\">Route component</a> as <code>this.props.match</code></li>\n<li><a href=\"/core/api/Route/render-func\" class=\"internal-link\">Route render</a> as <code>({ match }) =&gt; ()</code></li>\n<li><a href=\"/core/api/Route/children-func\" class=\"internal-link\">Route children</a> as <code>({ match }) =&gt; ()</code></li>\n<li><a href=\"/core/api/withRouter\" class=\"internal-link\">withRouter</a> as <code>this.props.match</code></li>\n<li><a href=\"/core/api/matchPath\" class=\"internal-link\">matchPath</a> as the return value</li>\n</ul>\n<p>If a Route does not have a <code>path</code>, and therefore always matches, you&#x2019;ll get the closest parent match. Same goes for <code>withRouter</code>.</p>\n<h2 id=\"match-null-matches\"><a href=\"/core/api/match/null-matches\" class=\"internal-link\">null matches</a></h2>\n<p>A <code>&lt;Route&gt;</code> that uses the <code>children</code> prop will call its <code>children</code> function even when the route&#x2019;s <code>path</code> does not match the current location. When this is the case, the <code>match</code> will be <code>null</code>. Being able to render a <code>&lt;Route&gt;</code>&apos;s contents when it does match can be useful, but certain challenges arise from this situation.</p>\n<p>The default way to &#x201C;resolve&#x201D; URLs is to join the <code>match.url</code> string to the &#x201C;relative&#x201D; path.</p>\n<pre><code class=\"language-js\"><span class=\"token template-string\"><span class=\"token string\">`</span><span class=\"token interpolation\"><span class=\"token interpolation-punctuation punctuation\">${</span>match<span class=\"token punctuation\">.</span>url<span class=\"token interpolation-punctuation punctuation\">}</span></span><span class=\"token string\">/relative-path`</span></span>\n</code></pre>\n<p>If you attempt to do this when the match is <code>null</code>, you will end up with a <code>TypeError</code>. This means that it is considered unsafe to attempt to join &#x201C;relative&#x201D; paths inside of a <code>&lt;Route&gt;</code> when using the <code>children</code> prop.</p>\n<p>A similar, but more subtle situation occurs when you use a pathless <code>&lt;Route&gt;</code> inside of a <code>&lt;Route&gt;</code> that generates a <code>null</code> match object.</p>\n<pre><code class=\"language-js\"><span class=\"token comment\">// location.pathname = &apos;/matches&apos;</span>\n<span class=\"token operator\">&lt;</span>Route path<span class=\"token operator\">=</span><span class=\"token string\">&apos;/does-not-match&apos;</span> children<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span> match <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=&gt;</span> <span class=\"token punctuation\">(</span>\n  <span class=\"token comment\">// match === null</span>\n  <span class=\"token operator\">&lt;</span>Route render<span class=\"token operator\">=</span><span class=\"token punctuation\">{</span><span class=\"token punctuation\">(</span><span class=\"token punctuation\">{</span> match<span class=\"token punctuation\">:</span>pathlessMatch <span class=\"token punctuation\">}</span><span class=\"token punctuation\">)</span> <span class=\"token operator\">=&gt;</span> <span class=\"token punctuation\">(</span>\n    <span class=\"token comment\">// pathlessMatch === ???</span>\n  <span class=\"token punctuation\">)</span><span class=\"token punctuation\">}</span><span class=\"token operator\">/</span><span class=\"token operator\">&gt;</span>\n<span class=\"token punctuation\">)</span><span class=\"token punctuation\">}</span><span class=\"token operator\">/</span><span class=\"token operator\">&gt;</span>\n</code></pre>\n<p>Pathless <code>&lt;Route&gt;</code>s inherit their <code>match</code> object from their parent. If their parent <code>match</code> is <code>null</code>, then their match will also be <code>null</code>. This means that a) any child routes/links will have to be absolute because there is no parent to resolve with and b) a pathless route whose parent <code>match</code> can be <code>null</code> will need to use the <code>children</code> prop to render.</p>\n","headers":[{"text":"null matches","slug":"null-matches"}],"title":{"text":"match","slug":"match"}}

